import { styled } from 'styled-components';

import * as device from '#theme/devices';

import Image from '../Image';

export const StyledMedia = styled.figure`
  margin: 0 0 ${({ theme }) => theme.spacing.x1}px 0;
`;

export const StyledImage = styled(Image)`
  display: block;
  aspect-ratio: 3 / 2;
  object-fit: cover;
`;

export const StyledCaption = styled.figcaption`
  padding: 6px ${({ theme }) => theme.spacing.x1_5}px;
  background-color: ${({ theme }) => theme.color.canvas.secondary};
  color: ${({ theme }) => theme.color.ink.muted};
  font: normal 14px/18px ${({ theme }) => theme.fontFamily.secondaryFont};
`;

export const StyledWrapper = styled.div`
  margin-bottom: ${({ theme }) => theme.spacing.x3}px;
`;

export const StyledList = styled.ol`
  --gap: ${({ theme }) => theme.spacing.x1}px;
  --cols: 1;
  --total-gap: calc(var(--gap) * (var(--cols) - 1));
  --item-width: calc((100% - var(--total-gap)) / var(--cols));
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap);
  padding: 0;
  margin: ${({ theme }) => theme.spacing.x1}px 0 0;
  counter-reset: list-counter;
  list-style: none;

  @media ${device.laptop} {
    --cols: 3;
  }

  > li {
    --margin-y: ${({ theme }) => theme.spacing.x1_5}px;
    --margin-x: ${({ theme }) => theme.spacing.x1_5}px;
    --counter-size: 32px;
    position: relative;
    flex: 0 1 var(--item-width);
    padding: 0;
    margin: 0;
    background-color: ${({ theme }) => theme.color.canvas.secondary};
    counter-increment: list-counter;

    &::before {
      --counter-color: ${({ theme }) => theme.color.actionBrand.base};
      display: flex;
      position: absolute;
      top: var(--margin-x);
      left: var(--margin-x);
      width: var(--counter-size);
      height: var(--counter-size);
      align-items: center;
      justify-content: center;
      border: 1px solid var(--counter-color);
      border-radius: 50%;
      color: var(--counter-color);
      content: counter(list-counter);
      font: bold 28px ${({ theme }) => theme.fontFamily.primaryFont};
    }

    @media ${device.laptop} {
      --margin-x: ${({ theme }) => theme.spacing.x2}px;
    }
  }
`;

export const StyledItem = styled.div`
  --line-height: 20px;
  margin: var(--margin-y) var(--margin-x) 0;
`;

export const StyledTitle = styled.h3`
  --margin-bottom: ${({ theme }) => theme.spacing.x0_5}px;
  margin: 0 0 var(--margin-bottom) calc(var(--margin-y) + var(--counter-size));
  color: ${({ theme }) => theme.color.ink.base};
  font: bold ${({ theme }) => theme.fontSize.base} / var(--line-height)
    ${({ theme }) => theme.fontFamily.secondaryFont};

  @media ${device.laptop} {
    --margin-bottom: ${({ theme }) => theme.spacing.x1}px;
    display: flex;
    min-height: calc(var(--line-height) * 2);
    align-items: center;
  }
`;

export const StyledBody = styled.p`
  margin: 0 0 var(--margin-y) calc(var(--margin-x) + var(--counter-size));
  color: ${({ theme }) => theme.color.ink.muted};
  font: normal ${({ theme }) => theme.fontSize.base} / var(--line-height)
    ${({ theme }) => theme.fontFamily.secondaryFont};

  @media ${device.laptop} {
    margin-left: 0;
  }
`;
