import { FC } from 'react';

import {
  StyledWrapper,
  StyledList,
  StyledItem,
  StyledTitle,
  StyledBody,
} from './MediaPlusList.styles';
import { MediaPlusListProps, Item } from './MediaPlusList.types';

const MediaPlusList: FC<MediaPlusListProps> = ({ media, items }) => {
  if (!media || items?.length !== 3) {
    return null;
  }

  return (
    <StyledWrapper>
      {media}
      <StyledList>
        {items.map(({ title, body }: Item, i: number) => (
          <li key={i}>
            <StyledItem>
              <StyledTitle>{title}</StyledTitle>
              <StyledBody dangerouslySetInnerHTML={{ __html: body }} />
            </StyledItem>
          </li>
        ))}
      </StyledList>
    </StyledWrapper>
  );
};

export default MediaPlusList;
